@layoutPrefix: kuma-layout;

.@{layoutPrefix}-2c {
    margin: 0 auto;
    /*  */
    .@{layoutPrefix}-2c-left-fixed {
        float: left;
        display: inline;
    }
    .@{layoutPrefix}-2c-right-fixed {
        float: right;
        display: inline;

    }
    /*  lf=left fixed */
    .@{layoutPrefix}-2c-left-lf {
      float: right;

    }
    .@{layoutPrefix}-2c-right-lf {
        width: 100%;
        float: right;
    }
    /*  rf=right fixed */
    .@{layoutPrefix}-2c-left-rf {
        width: 100%;
        float: left;
    }
    .@{layoutPrefix}-2c-right-rf {
        float: left;
    
    }
}

.@{layoutPrefix}-3c {
    margin: 0 auto;
    /*  */
    .@{layoutPrefix}-3c-fixed {
        float: left;
        display: inline;
    }
    /*  */
    .@{layoutPrefix}-3c-left {
        margin: 0 0 0 -100%;
        float: left;
        display: inline;
    }
    .@{layoutPrefix}-3c-mid {
        width: 100%;
        float: left;
        display: inline;

    }
    .@{layoutPrefix}-3c-right {
        float: left;
        display: inline;
    }
}


